import * as rpc from 'vscode-jsonrpc';
import * as net from 'net';


import { SudoPortForwarderService, initWorkspaceHubConnectionHub, LOGS_DIRECTORY } from '@vs/port-forwarder';
import { getServerArgOptions, startServer, ServerOptions } from '@vs/vso-workspace-hub-util';
import { SocketServer } from '@vs/vso-node-server';
import { TraceSource } from '@vs/vso-logging';

const argv = 
    getServerArgOptions()
    .option('workspaceId', {
        description: 'Workspace Id',
        alias: 'w',
        type: 'string'
    })
    .argv;

class PortAgentServer implements SocketServer {
    private sudoPortForwarderService: SudoPortForwarderService;

    constructor(traceSource: TraceSource) {
        if (!argv.workspaceId) {
            traceSource.error(`workspace id needs to be defined`);
            process.exit(2);
        }

        const connectionHub = initWorkspaceHubConnectionHub(traceSource);
        this.sudoPortForwarderService = new SudoPortForwarderService(
            traceSource,
            argv.workspaceId,
            connectionHub,
        );
    }

    public accept(socket: net.Socket): Promise<rpc.MessageConnection> {
        const messageReader = new rpc.StreamMessageReader(socket);
        const messageWriter = new rpc.StreamMessageWriter(socket);
        const rpcConnection =  this.sudoPortForwarderService.acceptStream({
            reader: messageReader,
            writer: messageWriter
        });

        return  Promise.resolve(rpcConnection);
    }

    public dispose(): Promise<void> {
        return this.sudoPortForwarderService.disconnectAsync();
    }
}

const options: ServerOptions = Object.assign({
    name: 'port-forwarder',
    processName: 'CodespacePortForwarder',
    logTmpDirectory: LOGS_DIRECTORY,
    isAdmin: true,
}, getServerArgOptions().argv);

startServer(options, (keepAliveCallback, traceSource) => new PortAgentServer(traceSource))
    .then((traceSource) => {
        traceSource.info('succeeded..');
    })
    .catch(err => {
        // Deal with the fact the chain failed
        console.error(`failed err:${err}`);
    });